.hello{
font-family: 'Roboto', sans-serif;}
* {
  margin: 0;
}

nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    background-color: rgb(16, 45, 70);
    color: whitesmoke;
}

.navlinks {
    width: 40%;
    display: flex;
    justify-content: space-evenly;
    list-style: none;
}

