a:visited{
  color: white;
}

a:link{
    color: white;
}



.shopContainer{

}

.catalogmenu{
    background-color: purple;
    list-style: none;
    min-height: 100%;
    width: 200px;
}

.catalog{
    display: flex;
}

.shopContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.labels {
    display: flex;
    flex-direction: column;
    font-family: 'Roboto', sans-serif;
    font-size: 40px; 
}

label.underline{
    text-decoration: overline;
}

.product{
    margin-left: 5%;
    margin-top: 5%;
}

.images {
    width: 300px;
    height: 300px;
}

html, body {
  height: 100%;
}

.wrapper {
  background: linear-gradient(to bottom right, #50a3a2 0%, #53e3a6 100%);
  min-height: calc(100% + 142px);
  margin-bottom: -142px;
}

.wrapper:after {
  content: "";
  height: 142px;
}

.footer {
  background-color: white;
  opacity: 0.5;
  height: 142px;
  text-align: center;
}
